html, body, #root, .App {
  height: 100%;
  background-color: #cccccc;
}

.App {
  text-align: center;
}

.Body {
  height: -webkit-calc(100% - 180px);
  height:    -moz-calc(100% - 180px);
  height:         calc(100% - 180px);
}

@media (max-width: 520px) {
  .Body {
    height: -webkit-calc(100% - 250px);
    height:    -moz-calc(100% - 250px);
    height:         calc(100% - 250px);
  }
}

.Home, .Projects, .About {
  height: 101%;
}

.Name {
  font: 400 90px/1.3 'Oleo Script', Helvetica, sans-serif;
  color: #2b2b2b;
  text-shadow: 4px 4px 0px rgba(0,0,0,0.1);
}

.First {
  margin-right: 20px;
}

.Header{
  min-height: 180px;
  background-color: #eeeeee;
  position: relative;
}

.HeaderLinks {
  position: absolute;
  bottom: 0;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.Header .Link {
  display: inline-block;
  margin-left: 7%;
  margin-right: 7%;
  margin-bottom: 10px;
}

.Header .Link a {
  color: inherit;
  text-decoration: none;
  font-size: larger;
  padding: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  /* border: solid gray 1px; */
  border-radius: 50%;
  background-color: #d3d3d3;
}

@media (max-width: 520px) {
  .Header{
    min-height: 250px;
  }

  .Name {
    padding-top: 10px;
    line-height: 80px;
  }

  .First, .Last {
    display: block;
    margin-right: 0;
  }

  .Header .Link {
    margin-left: 2%;
    margin-right: 2%;
  }
}

@media (max-width: 350px) {
  .Header .Link {
    margin-left: 1%;
    margin-right: 1%;
  }

  .Header .Link a {
    font-size: large;
    padding: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
  }
}

.Projects .Link {
  margin-bottom: 60px;
}

.Projects .Link a {
  color: inherit;
  text-decoration: none;
  width: 200px;
  display: inline-block;
  padding-top: 10px;
  padding-bottom: 10px;
  border: solid gray 1px;
  border-radius: 20%;
  background-color: darkgray;
}

.PageTitle {
  padding: 30px;
  font: 400 60px/1.3 'Oleo Script', Helvetica, sans-serif;
  color: #000000;
  text-shadow: 4px 4px 0px rgba(0,0,0,0.1);
}

.AboutMe {
  width: 60%;
  left: 20%;
  position: relative;
}

.AboutDescription {
  float: left;
  width: 60%;
  font-size: large;
}

.AboutPicture {
  float: right;
}

@media (max-width: 1100px) {
  .AboutMe {
    width: 70%;
    left: 15%;
  }
}

@media (max-width: 950px) {
  .AboutMe {
    width: 80%;
    left: 10%;
  }
}

@media (max-width: 800px) {
  .AboutMe {
    width: 90%;
    left: 5%;
  }

  .AboutDescription {
    width: 50%;
  }
}

@media (max-width: 600px) {
  .AboutMe {
    width: 90%;
    left: 5%;
  }

  .AboutDescription {
    float: none;
    width: 100%;
  }

  .AboutPicture {
    float: none;
    margin-bottom: 20px;
  }
}

@media (max-width: 280px) {
  .AboutPicture {
    display: none;
  }
}

.Snek table {
  padding-top: 4px;
  border-spacing: 4px 0;
}

.Snek table.center {
  margin-left: auto; 
  margin-right: auto;
}

.Snek {
  margin-bottom: 15px;
}

.SnekInfo {
  margin-left: 10px;
  margin-right: 10px;
}

.SnekMsg {
  margin-left: 30px;
}

.SnekInfoSection {
  margin-bottom: 5px;
}